@import url("https://fonts.googleapis.com/css2?family=Sora:wght@100;300;400;800&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}
::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}

body {
  font-size: 2rem;
  background-color: #000;
  font-family: "Sora", sans-serif;
  overflow-x: hidden;
  height: 100vh;
  width: 100vw;
}

.container {
  max-width: 114rem;
  margin: 0 auto;
  &__info {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &--logo {
      font-size: 3rem;
      padding: 2rem;

      .logo {
        color: #fff;
        text-decoration: none;
      }
    }

    &--btns {
      padding: 2rem;
    }
  }
  &__header {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-top: 7.5rem;
    &-text {
      display: flex;
      flex-direction: column;
      color: #fff;
      font-size: 7rem;
      letter-spacing: 1.7;
      text-align: center;
      font-weight: 800;

      &--sub {
        margin-top: 5rem;
        display: flex;
        flex-direction: column;
        color: #818181;
        font-weight: 300;
        text-align: center;
        font-size: 2.7rem;
      }
    }
  }
}

.container-2 {
  max-width: 114rem;
  margin: 80rem auto 0 auto;
  flex-direction: row;
  justify-content: space-around;

  &__improve {
    position: absolute;
    color: #fff;
    border: 1px solid rgba(255, 255, 255, 0.2);
    width: 42.5rem;
    height: 26rem;
    padding: 1rem;
    left: 54.6%;
    margin-top: 7rem;
    p {
      margin-top: 1.5rem;
      margin-bottom: 2rem;
      font-size: 1.6rem;
      font-weight: 300;
    }
    .gudie--btn {
      color: #000;
      background-color: #fff;
      padding: 1rem 2rem;
      font-size: 1.9rem;
      border-radius: 0.5rem;
      text-decoration: none;
      &:hover {
        background-color: transparent;
        color: #fff;
        border: 1px solid #fff;
      }
    }
  }

  &__descover {
    position: absolute;
    margin-left: 10rem;
    border: 1px solid rgba(255, 255, 255, 0.2);
    width: 42.5rem;
    height: 26rem;
    padding: 2.5rem;
    margin-top: 7rem;
    img {
      width: 15rem;
    }
    .perc {
      background: linear-gradient(to top, #fdc830, #f37335);
      background-clip: text;
      -webkit-text-fill-color: transparent;
      font-size: 7rem;
      font-weight: 800;
    }
    .normal {
      color: #fff;
      font-size: 2.5rem;
      max-width: 95%;
    }
  }

  &--1 {
    flex-direction: column;
  }
  &__box {
    position: absolute;
    left: 32.5%;
    transform: translateY(-70%);
    .box {
      border: 1px solid rgba(255, 255, 255, 0.2);
      width: 42.5rem;
      height: 26rem;
      transform: translate(75%, -22.5%);
      overflow: hidden;
      transition: all 0.2s;

      .image__box {
        width: 80rem;
        height: 80rem;
        top: -100%;
        left: -90%;
        position: relative;
        background-position: center;
        animation: rotate 20s linear infinite;
      }
    }
  }
  &__steps {
    .step {
      position: absolute;
      height: 80rem;
      transform: translateY(-20%);
      width: 2px;
      border-top-left-radius: 50%;
      border-top-right-radius: 50%;
      border-bottom-left-radius: 50%;
      border-bottom-right-radius: 50%;
      background-color: rgba(255, 255, 255, 0.4);
      &-num {
        width: 4.5rem;
        height: 4.5rem;
        border-radius: 50%;
        background-color: #fff;
        position: relative;
        left: -22.5px;
        top: 4.5rem;
        &--main {
          position: relative;
          font-size: 3rem;
          text-align: center;
          color: #000;
          top: 5px;
        }
      }
    }
  }
  &__text {
    color: #fff;
    margin-left: 10rem;
    justify-content: baseline;

    h2 {
      font-weight: 800;
      font-size: 4rem;
    }
    p {
      font-weight: 300;
      max-width: 35%;
      margin-top: 2rem;
      font-size: 2.1rem;
      color: rgba(255, 255, 255, 0.4);
    }

    .img-box {
      margin-top: 5rem;
      cursor: pointer;
      filter: brightness(0.5);
      overflow: hidden;
      width: 90rem;
      height: 50rem;
      transition: all 0.2s;
      .img:hover {
        transform: scale(1.2);
      }
      &:hover {
        filter: brightness(1);
      }
    }
  }
}

.Triangle-icon {
  color: #fff;
  font-size: 25rem;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
}
.btn {
  text-decoration: none;
  padding: 1rem 3rem;
  font-size: 1.9rem;
  border-radius: 5px;
  &-trail {
    background-color: #fff;
    color: #000;
    margin-right: 2rem;
  }

  &-trail:hover {
    background-color: transparent;
    color: #fff;
    border: 1px solid #fff;
  }

  &-Book {
    color: rgba(238, 238, 238, 0.5);
    border: 1px solid rgba(238, 238, 238, 0.5);
  }

  &-Book:hover {
    background-color: transparent;
    color: #fff;
    border: 1px solid #fff;
  }
}

i {
  position: absolute;
  width: 10px;
  height: 10px;
  opacity: 0.75;
  background: linear-gradient(transparent, #fff);
  border-radius: 50%;
  box-shadow: 0 0 1rem #fff, 0 0 2rem #fff, 0 0 4rem #fff, 0 0 8rem #fff;
  animation: animate 10s linear infinite;
}

@keyframes animate {
  0% {
    transform: translate(0);
  }

  100% {
    transform: translate(100%);
  }
}

@keyframes borderAnimate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.curser {
  position: absolute;
  width: 10rem;
  height: 10rem;

  span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid #fff;
    pointer-events: none;
    animation: borderAnimate 5s linear infinite;

    &:nth-child(1) {
      border-radius: 47% 53% 58% 42% / 44% 30% 70% 56%;
    }

    &:nth-child(2) {
      animation-direction: reverse;
      border-radius: 74% 26% 45% 55% / 28% 69% 31% 72%;
    }
    &:nth-child(3) {
      animation-duration: 3s;
      border-radius: 25% 75% 26% 74% / 66% 52% 48% 34%;
    }
  }
}
